import { useEffect, useState } from "react";

export default function useBookSearch(isMyRankClicked, pageNumber) {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);
  const [listData, setListData] = useState([]);
  // server  data
  const [hasMore, setHasMore] = useState(false);
  //   result   

  useEffect(() => {
    setListData([]);
  }, [isMyRankClicked]);
  // myRank render list   rank   render

  useEffect(() => {
    setLoading(true);
    setError(false);

    try {
      // fetchdata testcode
      const newListData = [];
      for (let i = 0; i < 10; i += 1) {
        newListData.push({
          isCurrentUser: false,
          avatarColor: "green",
          icon: "AVATAR_KIWI",
          score: 1000,
          nickname: `nickName ${i}`,
          ranking: i,
        });
      }
      // testcode end
      // then
      setLoading(true);
      //  page  data  ListData 
      setListData([...listData, ...newListData]);
      // fetch   list   hasMore set 
      setHasMore(true);
    } catch (e) {
      setError(true);
    }
  }, [isMyRankClicked, pageNumber]);

  return { loading, error, listData, hasMore };
}
